.about {
  padding-top: 25px;
  padding-bottom: 70px;
}

.about__text {
  padding: 25px 120px 25px 302px;

  background-image: url("../img/2005.png");
  background-repeat: no-repeat;
  background-position: center left 100px;

  @media (min-resolution: $resolution) {
    background-image: url("../img/2005@2x.png");
  }
}

.about__text h2 {
  margin-bottom: 18px;

  font-family: $roboto-condensed;
  font-weight: normal;
  font-size: 36px;
  line-height: 51px;

  color: var(--gray-black);
}

.about__text p {
  margin-bottom: 15px;

  font-family: $pt-mono;
  font-size: 16px;
  line-height: 22px;

  color: var(--gray-black);
}

.about__text a {
  display: flex;
  align-items: center;
  justify-content: center;

  width: 180px;
  min-height: 45px;

  font-family: $roboto-condensed;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.05em;

  text-align: center;
  text-transform: uppercase;

  color: var(--heavenly);

  border: 1px solid var(--heavenly);
  border-radius: 5px;
}
